<template>
    <div class="detail">
        <Mheader :back="true">详情页面</Mheader>
          <ul>
            <li>
              <img :src="book.bookCover">
            </li>
            <li>
              <label for="bookName">书的名称：</label>
              <input type="text" id="bookName" v-model="book.bookName">
            </li>
            <li>
              <label for="bookInfo">书的信息：</label>
              <input type="text" id="bookInfo" v-model="book.bookInfo">
            </li>
            <li>
              <label for="bookPrice">书的价格：</label>
              <input type="text" id="bookPrice" v-model.number="book.bookPrice">
            </li>
            <li>
              <button @click="update">确认修改</button>
            </li>
          </ul>
    </div>
</template>
<script>
import Mheader from "../base/Mheader.vue";
import { findOneBook, updateBook } from "../api";
export default {
  created() {
    //页面一加载，需要根据ID，发送请求
    this.getData();
  },
  data() {
    return { book: {} };
  },
  watch: {
    $route() {
      // 只要路径变化，重新获取数据
      this.getData();
    }
  },
  computed: {
    bid() {
      return this.$route.params.bid; //将路径参数的ID映射到bid上
    }
  },
  components: { Mheader },
  methods: {
    async getData() {
      // 通过ID找到某一项后赋予给book
      this.book = await findOneBook(this.bid);
      //如果是空对象,需要跳转回列表页
      Object.keys(this.book).length > 0 ? void 0 : this.$router.push("/list");
    },
    async update() {//点击修改图书信息
      await updateBook(this.bid,this.book);
      this.$router.push("/list");//修改完成后，跳转页面
    }
  }
};
</script>
<style scoped lang="less">
.detail {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 100;
  background: #fff;
}
ul {
  margin: 50px 20px 0 20px;
  li {
    img {
      margin: 5px auto;
      width: 50%;
      height: 220px;
    }
    label {
      display: block;
      font-size: 25px;
      color:purple;
      font-weight: bold;
    }
    input {
      margin: 10px 0;
      height: 35px;
      width: 100%;
      font-size: 25px;
    }
    button {
      font-size: 25px;
      display: block;
      width: 120px;
      height: 50px;
      background: #804000;
      color: white;
      border: none;
      border-radius: 15px;
      outline: none;
    }
  }
}
</style>



